import axios from 'axios';

let base = '';

export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getUserList = params => { return axios.get(`${base}/user/list`, { params: params }); };

export const getUserListPage = params => { return axios.get(`${base}/user/listpage`, { params: params }); };

export const removeUser = params => { return axios.get(`${base}/user/remove`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/user/batchremove`, { params: params }); };

export const editUser = params => { return axios.get(`${base}/user/edit`, { params: params }); };

export const addUser = params => { return axios.get(`${base}/user/add`, { params: params }); };



export const userlogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getUserListPageList = params => { return axios.post(`${base}/userList/pagelist`, params).then(res => res.data); };
export const getUserListAdd = params => { return axios.post(`${base}/userList/add`, params).then(res => res.data); };
export const getUserListUplate = params => { return axios.post(`${base}/userList/update`, params).then(res => res.data); };
export const getUserListRemove = params => { return axios.post(`${base}/userList/remove`, params).then(res => res.data); };
export const getUserListBatchRemove = params => { return axios.post(`${base}/userList/batchremove`, params).then(res => res.data); };

// export const getUserList = params => { return axios.get(`${base}/userList`, { params: params }); };
export const getArticlePageList = params => { return axios.get(`${base}/article/pagelist`, { params: params }); };
export const getArticleUplate = params => { return axios.post(`${base}/article/update`, params).then(res => res.data); };


